import React  from 'react';
import Figure from "./Figure";
import FigureWithTest from "./FigureWithTest";

import './Tab2.css'

function Tab2(props) {
    let mainRef = props.mainRef;
    let runs = mainRef['runs'].filter((item) => {
        return ('figure' in item &&
            (item['run_name'].startsWith('F1_') ||
            item['run_name'].startsWith('F3_') ||
            item['run_name'].startsWith('F4_') ||
            item['run_name'].startsWith('F5_') ||
            item['run_name'].startsWith('F6_') ||
            item['run_name'].startsWith('F7_')))
    });
    let runsDict = {};
    runs.forEach((item) => {
        let title = item['figure'];
        if (item['run_name'].startsWith('F1_')) {
            item['description'] = "Figure 1: Reward normalization ablation study of " +
                "RL2-PPO and MT-SAC success rates on ML10/MT10, in which one experiment " +
                "uses per-task reward normalization and the other does not. Vertical " +
                "lines indicate best epoch overall for each experimental variant. Mean " +
                "and 95% confidence interval for five seeds. We find a statistically " +
                "significant result that reward scaling improves the peak performance of " +
                "RL2-PPO (p = 0.04%).";
            item['fixy'] = 45;
            item['fixx'] = 90000000;
        }
        if (item['run_name'].startsWith('F3_')) {
            item['description'] = "Figure 3: Ablation study of environment time limits on PEARL and MT-SAC " +
                "success rates on ML10/MT10. By removing the terminal signal at the time limit, " +
                "MT-SAC performance improves (p = 1.5%) by 110%. Mean and 95% confidence interval " +
                "using 4 seeds";
            item['fixx'] = 10000000;
            item['fixy'] = 80;
        }
        if (item['run_name'].startsWith('F4_')) {
            item['description'] = "Figure 4: Ablation study of MaxEnt on RL2-PPO and MT-PPO. We find " +
                "statistically significant evidence that MaxEnt prevents RL2-PPO from reaching " +
                "its peak performance (p = 0.6%). Horizontal lines indicate the mean performance " +
                "measure for each experiment variant. Mean and 95% confidence interval using 5 seeds.";
            item['fixy'] = 30;
            item['fixx'] = 90000000;
        }
        if (item['run_name'].startsWith('F5_')) {
            item['description'] = "Figure 5: Comparison of using TRPO or PPO as the \"subroutine algorithm\" " +
                "for RL2 and MAML. As can clearly be seen in the graph, the selection of subroutine " +
                "algorithm has a significant effect on performance (p = 6.3 times 10^(-6) for RL2 and p = 0.016%" +
                " for MAML). Mean and 95% confidence interval for 5 seeds.";
            item['fixy'] = 25;
            item['fixx'] = 50000000;
        }
        if (item['run_name'].startsWith('F6_')) {
            item['test'] = (item['algo'].includes('test'));
            item['description'] = "Figure 6: Return processing ablation study for RL2-PPO, with a single " +
                "line-difference on how the algorithm processes returns. Mean and 95% confidence interval " +
                "across 5 random seeds.";
            item['fixy'] = 30;
            item['fixx'] = 90000000;
            item['fixytest'] = 40;

        }
        if (item['run_name'].startsWith('F7_')) {
            item['description'] = "Figure 7: Adaptation sample efficiency of MAML-TRPO, RL2-PPO, " +
                "and PEARL on the ML45 benchmark. Mean and 95% confidence interval for at least " +
                "9 seeds.";
            item['fixy'] = 30;
            item['fixx'] = Math.pow(2, 13);
        }

        item['data'] = require('../results/' + item['run_name'] + '.json');
        if (title in runsDict) {
            runsDict[title].push(item);
        }
        else {
            runsDict[title] = [item]
        }
    });

    return (
        <section>
            {Object.keys(runsDict).map((title, idx) => {
                if (runsDict[title][0]['run_name'].startsWith('F6_')) {
                    return (
                        <div key={idx} className="figure-with-tests">
                            <FigureWithTest runs={runsDict[title]} title={title} />
                            <hr className="hr-with-tests" />
                        </div>
                    )
                }
                else {
                    return (
                        <div key={idx} className="figure">
                            <Figure runs={runsDict[title]} title={title} />
                            <hr className="hr" />
                        </div>
                    )
                }
            })}
        </section>
    );
}

export default Tab2;
