import React from 'react';
import Figure from "./Figure";

import './Tab4.css';
import FigureWithTest from "./FigureWithTest";

function Tab4(props) {
    let mainRef = props.mainRef;
    let runs = mainRef['runs'].filter((item) => {
        return ('figure' in item &&
            (item['run_name'].startsWith('F12_') ||
                item['run_name'].startsWith('F13_') ||
                item['run_name'].startsWith('F14_') ||
                item['run_name'].startsWith('F15_') ||
                item['run_name'].startsWith('F16_') ||
                item['run_name'].startsWith('F17_') ||
                item['run_name'].startsWith('F18_') ||
                item['run_name'].startsWith('F19_') ||
                item['run_name'].startsWith('F20_') ||
                item['run_name'].startsWith('F21_') ||
                item['run_name'].startsWith('F22_')))
    });
    let runsDict = {};
    runs.forEach((item) => {
        let title = item['figure'];
        if (item['run_name'].startsWith('F12_')) {
            item['description'] = "Figure 12: Comparison of our implementation of MT-PPO to one " +
                "based on openai/baselines. Mean and 95% confidence interval over 10 random seeds.";
            item['fixy'] = 10;
            item['fixx'] = 6000000;
        }
        if (item['run_name'].startsWith('F13_')) {
            item['description'] = "Figure 13: Comparison of our implementation of MT-TRPO to one " +
                "based on openai/baselines. Mean and 95% confidence interval over 10 random seeds.";
            item['fixy'] = 35;
            item['fixx'] = 6000000;
        }
        if (item['run_name'].startsWith('F14_')) {
            item['description'] = "Figure 14: Comparison of our implementation of MT-SAC to ones " +
                "based on rlkit. Mean and 95% confidence interval over >= 5 random seeds.";
            item['fixy'] = 65;
            item['fixx'] = 10000000;
        }
        if (item['run_name'].startsWith('F15_')) {
            item['description'] = "Figure 15: Comparison of our implementation of MT-SAC to ones " +
                "based on rlkit. Mean and 95% confidence interval over >= 5 random seeds.";
            item['fixy'] = 25;
            item['fixx'] = 10000000;
        }
        if (item['run_name'].startsWith('F16_')) {
            item['test'] = (item['algo'].includes('test'));
            item['description'] = "Figure 16: Comparison of our implementations of MAML-TRPO to the " +
                "implementation from widely-cited implementation in ProMP Rothfuss et al., (2018) on " +
                "the ML1-push benchmark from Yu et al., (2019). Our implementation exhibits higher " +
                "meta-train success rates on this benchmark (p=5.7%), but the change in meta-test " +
                "success rates is not statistically significant (p=5.%). Mean and 95% confidence " +
                "interval over 5 random seeds.";
            item['fixy'] = 60;
            item['fixx'] = 50000000;
            item['fixytest'] = 80;
        }
        if (item['run_name'].startsWith('F17_')) {
            item['test'] = (item['algo'].includes('test'));
            item['description'] = "Figure 17: Comparison of our implementations of MAML-TRPO to the " +
                "implementation from widely-cited implementation in ProMP Rothfuss et al., (2018) on " +
                "the ML1-reach benchmark from Yu et al., (2019). Our implementation performs similarly " +
                "to the baseline, although it exhibits higher variance in the meta-test success rate. " +
                "Mean and 95% confidence interval over 5 random seeds.";
            item['fixy'] = 25;
            item['fixx'] = 50000000;
            item['fixytest'] = 60;
        }
        if (item['run_name'].startsWith('F18_')) {
            item['test'] = (item['algo'].includes('test'));
            item['description'] = "Figure 18: Comparison of our implementation of RL2-PPO to the " +
                "widely-cited implementation in ProMP Rothfuss et al., (2018) on the ML1-push " +
                "benchmark from Yu et al., (2019). Mean and 95% confidence interval over 5 random " +
                "seeds. Both implementations have equal asymptotic performance, although ours " +
                "exhibits slightly faster rise time. Specifically, at 15 million total environment " +
                "steps, our implementation has a slightly higher meta-train success rate (p=3.2%)";
            item['fixy'] = 100;
            item['fixx'] = 60000000;
            item['fixytest'] = 100;
        }
        if (item['run_name'].startsWith('F19_')) {
            item['test'] = (item['algo'].includes('test'));
            item['description'] = "Figure 19: Comparison of our implementation of RL2-PPO to the " +
                "widely-cited implementation in ProMP Rothfuss et al., (2018) on the ML1-reach " +
                "benchmark from Yu et al., (2019). Mean and 95% confidence interval over 5 random " +
                "seeds. Both implementations have equal asymptotic performance, although ours " +
                "exhibits measurably faster rise time. Specifically, at 15 million total environment " +
                "steps, our implementation has a slightly higher meta-train success rate (p=0.8%) " +
                "and meta-test success rate (p=1.9%).";
            item['fixy'] = 90;
            item['fixx'] = 60000000;
            item['fixytest'] = 90;
        }
        if (item['run_name'].startsWith('F20_')) {
            item['test'] = (item['algo'].includes('test'));
            item['description'] = "Figure 20: Comparison of our implementation of PEARL-SAC to the " +
                "Oyster repository Rakelly et al., (2019) on the ML1-push benchmark from Yu et al., (2019). " +
                "The two implementations appear to perform similarly. Mean and 95% confidence interval " +
                "over 5 random seeds.";
            item['fixy'] = 70;
            item['fixx'] = 5000000;
            item['fixytest'] = 70;
        }
        if (item['run_name'].startsWith('F21_')) {
            item['test'] = (item['algo'].includes('test'));
            item['description'] = "Figure 21: Comparison of our implementation of PEARL-SAC to the " +
                "Oyster repository Rakelly et al., (2019) on the ML1-reach benchmark from Yu et al., (2019). " +
                "The two implementations appear to perform similarly. Mean and 95% confidence interval " +
                "over 5 random seeds.";
            item['fixy'] = 30;
            item['fixx'] = 5000000;
            item['fixytest'] = 30;
        }
        if (item['run_name'].startsWith('F22_')) {
            item['test'] = (item['algo'].includes('test'));
            item['description'] = "Figure 22: Comparison of our implementation of PEARL-SAC to the " +
                "Oyster repository Rakelly et al., (2019) on the ML1-pick-place benchmark from Yu et al., (2019). " +
                "The two implementations appear to perform similarly. Mean and 95% confidence interval " +
                "over 5 random seeds.";
            item['fixy'] = 40;
            item['fixx'] = 5000000;
            item['fixytest'] = 40;
        }

        item['data'] = require('../results/' + item['run_name'] + '.json');
        if (title in runsDict) {
            runsDict[title].push(item);
        }
        else {
            runsDict[title] = [item]
        }
    });

    return (
        <section>
            {Object.keys(runsDict).map((title, idx) => {
                if (runsDict[title][0]['run_name'].startsWith('F16_') ||
                    runsDict[title][0]['run_name'].startsWith('F17_') ||
                    runsDict[title][0]['run_name'].startsWith('F18_') ||
                    runsDict[title][0]['run_name'].startsWith('F19_') ||
                    runsDict[title][0]['run_name'].startsWith('F20_') ||
                    runsDict[title][0]['run_name'].startsWith('F21_') ||
                    runsDict[title][0]['run_name'].startsWith('F22_')) {
                    return (
                        <div key={idx} className="figure-with-tests">
                            <FigureWithTest runs={runsDict[title]} title={title} />
                            <hr className="hr-with-tests" />
                        </div>
                    )
                }
                else {
                    return (
                        <div key={idx} className="figure">
                            <Figure runs={runsDict[title]} title={title} />
                            <hr className="hr" />
                        </div>
                    )
                }
            })}
        </section>
    );
}

export default Tab4;
